	.van-field__body {
		height: 22px;
		input {
			line-height: normal;
		}
	}
}
@keyframes myfirst {
	0% {
		opacity: 0.5;
		padding-top: 0;
		padding-bottom: 20px;
	}
	5% {
		opacity: 1;
		padding-top: 0;
